.work {
  background-color: crimson;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.arrow {
  position: absolute;
  cursor: pointer;
  transition: all ease-in-out 0.4s;
}

.arrow:hover img {
  transform: translateX(20px);
}

.arrow img {
  height: 50px;
  transition: all ease-in-out 0.4s;
}

.arrow-left {
  left: 10vw;
  transform: rotateY(180deg);
}

.arrow-right {
  right: 10vw;
}

.work-slider {
  height: 400px;
  display: flex;
  position: absolute;
  left: 0;
  transition: all ease-out 0.5s;
}

.work-container {
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.index {
  position: absolute;
  bottom: -15%;
  font-size: 2rem;
  color: white;
  font-weight: 500;
}

.work-item {
  width: 700px;
  height: 100%;
  background-color: white;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  position: relative;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.5);
}

.item-left {
  flex: 4;
  height: 100%;
  background-color: rgb(214, 107, 68);
  display: flex;
  align-items: center;
  justify-content: center;
}

.item-left .leftContainer {
  width: 90%;
  height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.work-img {
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.work-img img {
  width: 60px;
  height: 85%;
  object-fit: cover;
}

.leftContainer h2 {
  font-size: 25px;
  font-weight: 500;
}

.leftContainer p {
  font-size: 18px;
  font-weight: 400;
}

.leftContainer span {
  background-color: crimson;
  padding: 0.4rem 1rem;
  text-align: center;
  font-size: 1rem;
  font-weight: 500;
  color: white;
  cursor: pointer;
  border-radius: 10px;
  transition: all ease-out 0.3s;
  margin-top: 10px;
}

.leftContainer span:hover {
  transform: scale(1.04);
}

.item-right {
  flex: 8;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.rightImgCon {
  width: 450px;
  height: 450px;
}

.rightImgCon img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transform: rotate(-10deg);
}

@media screen and (max-width: 850px) {
  .work-container {
    width: 100vw;
    height: 100%;
  }
  .work-item {
    position: absolute;
    width: 85%;
    height: 200px;
    top: 10px;
  }

  .item-left {
    width: 100%;
    height: 100%;
  }

  .rightImgCon {
    width: 250px;
    height: 300px;
  }

  .rightImgCon img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    transform: rotate(-10deg);
  }

  .arrow {
    position: absolute;
    bottom: 80px;
  }

  .arrow img {
    height: 40px;
  }

  .work-img {
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .work-img img {
    width: 30px;
    height: 85%;
    object-fit: cover;
  }
  .item-left .leftContainer {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: crimson;
  }
  
  .leftContainer h2{
      font-size: 15px;
      font-weight: 500;
      text-align: center;
      color: white;
    }
    
    .leftContainer p{
        display: none;
    }
    
    .leftContainer span{
        background-color: rgb(37, 1, 8);
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        height:50px;
        font-size: 12px;
        font-weight: 400;
        color: white;
        cursor: pointer;
        border-radius: 0;
        transition: all ease-out 0.3s;
    }

    .index{
        position: absolute;
        bottom: 0;
    }
}
