.portfolio {
  background-color: rgb(44, 39, 39);
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding-top: 2rem;
  position: relative;
  overflow: hidden;
}

.portfolio h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
  color: rgb(253, 103, 130);
  font-weight: 500;
  text-transform: uppercase;
}

.portfolio ul {
  display: flex;
  list-style-type: none;
  align-items: center;
}

.container {
  margin-top: 3rem;
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding: 10px 25px;
}

.item {
  width: 250px;
  height: 180px;
  margin-right: 4rem;
  margin-bottom: 2rem;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  border-radius: 30px;
  transition: all ease-in-out 0.4s;
  border-radius: 10px;
  cursor: pointer;
}

.item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all ease-in-out 0.4s;
  border-radius: 10px;
  box-shadow: 3px 3px 9px rgba(0, 0, 0, 1);
}

.item h3 {
  position: absolute;
  font-size: 1rem;
  padding: 0;
  z-index: -1;
  font-weight: 500;
  transition: all ease-in-out 0.4s;
}

.item:hover img {
  opacity: 0.3;
  transform: scale(1.04);
}
.item:hover {
  background-color: rebeccapurple;
}
.item:hover h3 {
  z-index: 3;
  color: white;
  font-weight: 700;
}

@media screen and (max-width: 1210px) {
  .container {
    width: 100%;
    height: 70%;
  }

  .portfolio ul {
    width: 100%;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding: 0;
  }
}

@media screen and (max-width: 850px) {

  .container{
    /* height: auto; */
    width: 100%;
    padding: 0;
    margin: 0.8rem;
  }

  .item{
    width: 150px;
    height: 100px;
    margin: 10px 10px;
  }

}
